@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(190, 190, 190); 
    -webkit-backdrop-filter: blur(50px); 
            backdrop-filter: blur(50px); 
    height: 6rem;
    background-color: white;
}
a{
    color: inherit;
}
.item1{
    border: none;
    background-color: inherit;
    color: #03bff9;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.item1:hover {
    text-decoration: none;
    color: #48976a;
}
.item1:focus {
    outline: 0;
}
.item2 {
    color: rgb(243, 244, 244);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px); 
    font-size: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
   
}
.designer{
    color: #03bff9;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px); 
    font-size: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;


}

.footer-container {
    align-items: center;
    align-content: center;
    text-align: left;
    left: 0;
    bottom: 0;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px); 
    font-size: 13px;
    width: 100%;
    height: 3rem;
    display: grid;
    grid-template-columns: 6fr .5fr 6fr 8fr;
    color: rgb(118, 124, 129);
    grid-template-areas: footer; 
    position: fixed;
    left: 0;
    bottom: 0;
    

}


p{
    align-items: center;
    justify-content: center;
    text-align: center;
}
@media only screen and (min-width: 600px) {
    .item1 {
        font-size: 14px;
        padding-left: 5px;
    }
    .item2 {
        font-size: 9px;
    }
}
@media only screen and (min-width: 768px) {
    .item1 {
        font-size: 16px;
        padding-left: 6px;
    }
    .item2 {
        font-size: 11px;
    }
}
@media only screen and (min-width: 992px) {
    .item1 {
        font-size: 18px;
        padding-left: 8px;
    }
    .item2 {
        font-size: 13px;
    }
}
@media only screen and (min-width: 1200px) {
    .item1 {
        font-size: 20px;
        padding-left: 10px;
    }
    .item2 {
        font-size: 15px;
    }
}

.map-main {
  display: flex;
  height: 89vh;
  width: 100vw;
  position: relative;
 
  place-items: center;
  justify-content: center;
  align-items: center;
  margin: auto 0;
  
 
  }
body {

  margin: 0;
  font-family: 'Poppins', sans-serif, 500;
}

.cluster-marker {

  color: white;
  background: rgba(22, 140, 280, 240);
  opacity:80%;
  font-size: 12px;
  border-radius: 50%;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.infoBox {

  position: absolute;
  top: 20px;
  right: 50px;
  width: 300px;
  min-height: 200px;
  padding: 20px;
  /* background-color: black; */
  border-radius: 10px;
  font-size: 10px;
  font-size: 18px;
  color: black;
  font-family: 'Poppins', sans-serif, 500;
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border-color: black;
  border: 3px;
  text-align: left;


}
header{
  text-align: center;
  text-decoration: underline;
}


nav{
    background: rgba(0,0,0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.9rem 0.2rem;
    z-index: 2;
    position: fixed;
    left: -360px;
    bottom: 50%; 
    -webkit-transform: translateX(570%); 
            transform: translateX(570%);
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 9rem;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    

}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 30%;
    font-size: 1.1rem;
    display: flex;
    color: var(--color-light);
}
nav a:hover{
    background: rgba(0,0,0,0.3);

}
nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}
